import React from 'react';
import Button from '@material-ui/core/Button';
import {withStyles, withTheme} from "@material-ui/core/styles";
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import MenuList from '@material-ui/core/MenuList';
import icon from "../select.svg";

class Tile extends React.Component {

    render() {

        const { classes } = this.props;
        return (
            <div className={classes.tileWrapper}>
                <div className={classes.titleWrapper}>
                    <span className={classes.title}>{this.props.title}</span>
                </div>
                <div className={classes.dataDiv}>
                    <div className={classes.keys}>
                        {this.props.keys.map((value, key) => {return <div className={classes.keyItem} key={key}>{value}</div>})}
                    </div>
                    <div className={classes.values}>
                        {this.props.values.map((value, key) => {return <div className={classes.valueItem} key={key}>{value}</div>})}
                    </div>
                </div>

            </div>
        );
    }
}

const styles = theme => ({
    dataDiv:{
        display:"flex",
        marginTop: "2%",
    },
    keys:{
        width:"90%",
        fontWeight: "300",
        lineHeight: "200%",
        fontSize: 18

},
    values:{
        fontWeight: "500",
        lineHeight: "173%",
        fontSize: 22
    },
    valueItem:{},
    keyItem:{},
    title:{
        fontSize: 20,
        fontWeight: "500"
    },
    titleWrapper:{},
    selected:{

    },

    tileWrapper: {
        width: "100%",
        background: "#FFFFFF",
        boxShadow: "0px 1px 4px #E5E9F2",
        borderRadius: 5,
        marginTop: 10,
        //paddingTop: "2%",
        paddingTop: 20,
        paddingLeft: "2.3%",
        paddingRight: "2.3%",
        "&:first-child":{
            marginRight: 5,
        },
        "&:last-child":{
            marginLeft: 5
        }
    },
    '@media only screen and (max-height: 940px)': {
        dataDiv:{
            display:"flex",
            marginTop: "4%"
        },
        keys:{
            width:"90%",
            fontWeight: "300",
            lineHeight: "150%",
            fontSize: 16

        },
        values:{
            fontWeight: "500",
            lineHeight: "118%",
            fontSize: 20
        },
    }
});

export default withStyles(styles)(withTheme()(Tile));


