import React, { Component } from 'react';
import {withStyles, withTheme} from "@material-ui/core/styles";

class FormItem extends Component {
    constructor(props) {
        super(props);
        this.state = {icon: require(`../icons/${this.props.id}.svg`)};
    }
    menter = () => {
        this.setState({icon: require(`../icons/${this.props.id}_alt.svg`)});
    };

    mleave = () => {
        this.setState({icon: require(`../icons/${this.props.id}.svg`)});
    };

    render() {
        const { classes } = this.props;
        const icon_alt = require(`../icons/${this.props.id}_alt.svg`);
        const icon = require(`../icons/${this.props.id}.svg`);
        return (
        <div className={classes.formWrapper} onMouseEnter={this.menter} onMouseLeave={this.mleave}>
            {/*<object type={"image/svg+xml"} data={this.state.icon} className={classes.icon} />*/}
            <img className={classes.icon} src={this.state.icon}/>
            <span className={classes.formText}>{this.props.text}</span>
        </div>
        )
    }
}

const styles = theme => ({
    formText: {
        fontFamily: "Roboto",
        fontStyle: "normal",
        fontWeight: 300,
        lineHeight: "32px",
        fontSize: 16,
        color: "#000000"

    },
    formWrapper: {
        display: "flex",
        marginTop: 32,
        width: "95%",
        padding: "12px 0 12px",
        "&:hover":{
            background: "linear-gradient(180deg, #679CF6 0%, #4072EE 100%)",
            boxShadow: "0px 4px 4px rgba(116, 138, 161, 0.25)",
            borderRadius: "0 5px 5px 0",
            "& $formText":{
                color: "white"
            }

        }
    },
    icon: {
        width: 30,
        height:30,
        marginRight: 15,
        marginLeft: 22,
    }
});

export default withStyles(styles)(withTheme()(FormItem));


