import React, { Component } from 'react';
import {withStyles} from '@material-ui/core/styles';
import UserMenu from "./components/userMenu";
import HeaderMenu from "./components/HeaderMenu";
import Tile from "./components/tile";
import Plot from "./components/plot";



class App extends Component {
    constructor(props) {
        super(props);

        //Отключить дотошный скролл на ipad
        document.querySelector('body').addEventListener('touchmove', function(e) {
          e.preventDefault();
        }, { passive: false });

        //Заряжаем COPY_TO_TEMP
        //Biscuit.request("ACTION_START_APP", {}, undefined, true)
    };

    render() {
        const { classes } = this.props;

        const keys1 = ["На согласовании", "Более 2-х дней", "Согласовано за 3-й кв. 2018 г", "Согласовано с нар. ср. за 3-й кв. 2018"];
        const values1 = ["242", "23", "-", "14"];
        const keys3 = ["На согласовании", "Более 2-х дней", "Согласовано за 3-й кв. 2018 г", "Согласовано с нар. ср. за 3-й кв. 2018"];
        const values3 = ["242", "23", "242", "-"];
        const keys2 = ["Статья бюджета", "Центр затрат"];
        const values2 = ["277", "230"];
        const keys4 = ["Центр затрат", "МВЗ", "ОП по заводам", "Резервирование"];
        const values4 = ["277", "230", " ", " "];

        //this.refs.plot.style.top = this.refs.tiles.clientHeight + 10 + "px";
        //console.log(this.refs.tiles.clientHeight);

        console.log(window.bobrPath);

        return(
                <div className={
                  classes.appBackground
                } >
                   <UserMenu />
                    <div className={classes.verticalWrapper}>
                        <div className={classes.headerDiv}>
                            <HeaderMenu/>
                        </div>
                        <div className={classes.dynamicDiv}>
                            <div className={classes.allTilesWrapper}>
                                <div className={classes.tileRow}>
                                    <Tile title={"ЗНК"} keys={keys1} values={values1}/>
                                    <Tile title={"Устаревшие значения"} keys={keys2} values={values2}/>
                                </div>
                                <div className={classes.tileRow}>
                                    <Tile title={"Договоры"}  keys={keys3} values={values3}/>
                                    <Tile title={"Отрицательные остатки"}  keys={keys4} values={values4}/>
                                </div>
                            </div>
                            <div className={classes.plotDiv}>
                                <Plot/>
                            </div>
                        </div>
                    </div>
                </div>
        );
    }
}

const styles = theme => ({
    dynamicDiv:{
        position: "absolute",
        left: 0,
        right: 0,
        top: "4.875em",
        bottom: 0,
        minWidth: "54.375em"
    },
    headerDiv:{
        height: "4.875em",
        width: "100%",
        minWidth: "54.375em"
    },
    allTilesWrapper:{
        width:"100%",
        height:"50%",
        minHeight: "21.875em"
    },
    tileRow:{
        display:"flex",
        width: "100%",
        height: "50%"
    },
    verticalWrapper:{
        display: "block",
        width: "100%",
        position:"relative",
        marginLeft: "0.3125em",//
        marginRight: "0.625em"
    },
    appBackground: {
        top:0,
        left:0,
        right:0,
        bottom:0,
        transition: "all 300ms ease-in-out",
        position: "absolute",
        backgroundColor: theme.palette.primary.mainBackground,
        display: "flex",
        fontFamily: "Roboto",
        //overflowY: "hidden",
        overflowY: "scroll",
        overflowX: "scroll"
    },
    plotDiv:{
        height: "48%",
        width: "100%",
        // bottom:0,
        // left:0,
        // right:0,
        position: "absolute",
        minHeight: "18.75em"
    },
  appOutput: {
    width: "calc(100% - 0px)",
    transition: "all 300ms ease-in-out",
    position: "absolute",
    height: "100%",
      backgroundColor: theme.palette.primary.tiles,
    "&.active": {
      width: "calc(100% - 16.625em)"
    },
    "&:not(.active) header" : {
      width: "100%"
    },
    "&.alternative": {
      right: 0
    },
    "&.alternative header": {
      left: "-15.625em"
    },
    "&.alternative.active": {
      width: "calc(100% - 15.625em)"
    },
    "&.alternative:not(.active) header": {
      width: "calc(100% - 0px)",
      left: 0
    }
  },
  appMenuOutput: {
    width: "13.75em",
    position: "absolute",
    top: 0,
    height: "100%",
    background: theme.palette.primary.header,
    padding: "50px 15px 0 15px",
    transition: "all 300ms ease-in-out",
    transform: "translateX(255px)",
    right: "-15.625em",
    "&.active" : {
      transform: "translateX(0)"
    },
    "&.alternative": {
      right: "auto",
      left: "-15.625em"
    },
    "&.alternative:not(.active)": {
      transform: "translateX(-15.9375em)"
    }
  },
  barWrapper: {
    height: "74.5%",
    margin:"0 -1em",
    overflowY: "hidden",
    overflowX: "visible"
  },
    drivers: {
      touchAction: "none"
    },
  btnPanel: {
    bottom: "4.563em",
    width: "calc(100% - 1.875em)",
    position: "absolute",
    "& > button": {
      width: "calc(100% - 1.875em)",
      minHeight: "2em",
      margin: "10px 0.3125em auto 1em",
      padding: "0.5em",
      textAlign: "center",
      textTransform: "none",
    },
    "& >button span": {
      textAlign: "center",
      fontWeight: "25em",
      width: "100%",
      display: "block"
    }
  }
});//

export default withStyles(styles)(App);




