import React, {Component} from "react";
import {withStyles, withTheme} from "@material-ui/core/styles";
import Button from '@material-ui/core/Button';
import Select from "./Select";//
import user from "../User.png";
import FormItem from "./formItem";

class HeaderMenu extends Component {

    render() {
        const { classes } = this.props;

        console.log(classes.outlined);

        return (
            <div className={classes.headerMenuWrapper}>
                <Button variant="outlined" classes={{outlined: `${classes.outlined} ${classes.employee}`, root: classes.root}} TouchRippleProps={{classes:{ripple:classes.rippleEmployee}}}>
                    Сотрудник
                </Button>
                <Button variant="outlined" classes={{outlined: `${classes.outlined} ${classes.department}`, root: classes.root}} TouchRippleProps={{classes:{ripple:classes.rippleDepartment}}}>
                    Подравзделение
                </Button>
                <div className={classes.selectPanelWrapper}>
                    <Select className={classes.select} currVal={"Подкатегории бюджета"} items={["IT", "Транспорт", "Безопасность"]} />
                    <Select currVal={"2019"} items={["2018", "2019", "2020", "2021"]}/>
                    <Select currVal={"III кв."} items={["I кв.", "II кв.", "III кв.", "IV кв."]}/>
                    <Select currVal={"Меню."} items={["Пункт 1", "Пункт 2", "Пункт 3", "Пункт 4"]}/>
                    <Button variant="outlined" classes={{outlined: classes.update, root: classes.root, label:classes.updateLabel}}>
                        Обновить
                    </Button>
                </div>
            </div>
        )
    }
}

const styles = theme => ({
    selectPanelWrapper:{
        position: "absolute",
        right: "0.625em",
        display: "flex"
    },
    rippleDepartment:{
        color: "#3d7a00"
    },
    rippleEmployee:{
        color: "#3258ae"
    },
    updateLabel:{

    },
    update:{
        padding:"0.125em",
        border: "0.047em solid #748AA1",
        borderRadius: "1.25em",
        width: "5.625em",
        height: "1.5625em",
        color: "#748AA1",
        fontSize: "0.875em",
        marginTop: "1.5625em",
        lineHeight: "0.625em",
        minHeight: "0.3125em",
        minWidth: "5.625em",
        marginLeft: "1.25em",
        "&:active":{
            boxShadow: "0px 0.125em 0.25em rgba(0, 0, 0, 0.15),0.125em 0px 0.25em rgba(0, 0, 0, 0.15),-0.125em 0px 0.25em rgba(0, 0, 0, 0.15)",
        },
        "&:hover":{
            backgroundColor: "#29CB97",
            borderColor: "#29CB97",
            color: "#FFFFFF",
            boxShadow: "0px 0.25em 0.25em rgba(0, 0, 0, 0.25)"
        },
    },
    employee:{
        marginLeft: "1.75em",
        border: '0.046875em solid #4072EE',
        "&:hover":{
            backgroundColor: "#4072EE",
            color: "#FFFFFF",
            boxShadow: "0px 0.25em 0.25em rgba(116, 138, 161, 0.25)"
        },
    },
    department:{
        marginLeft: "0.625em",
        border: '0.046875em solid #4B9500',
        "&:hover":{
            backgroundColor: "#4B9500",
            color: "#FFFFFF",
            boxShadow: "0px 0.25em 0.25em rgba(116, 138, 161, 0.25)"
        },
    },
    outlined:{
        width: "9.3125em",
        height: "2.5em",
        color: "#748AA1",
        fontSize: "0.875em",
        marginTop: "1.1875em",
        borderRadius: '0.3125em',
        minWidth: "8.25em",
        lineHeight: "1em"
    },
    root: {
        textTransform: 'none',

    },
    headerMenuWrapper: {
        backgroundColor: theme.palette.primary.userMenuBackground,
        background: '#FFFFFF',
        boxShadow: '0px 0.0625em 0.25em #E5E9F2',
        borderRadius: '0px 0px 0.3125em 0.3125em',
        //transform: 'matrix(1, 0, 0, -1, 0, 0)',
        //border:"5px solid black",
        height:"100%",
        borderTop: "none",
        display: "flex",
        position: "relative"
    },

    tileInner: {
        width: "100%",//
        position: "relative",
        height: "100%",
        boxSizing: "border-box",
        background: theme.palette.primary.tiles,
        padding: "0.3125em"
    },
});

export default withStyles(styles)(withTheme()(HeaderMenu));


