import connect from "react-redux/es/connect/connect";
import App from '../App';
import {bindApp, bindReq, bindUpdate, bindDrivers} from "../actions";

function mapStateToProps (payload) {
  return {
    storeApp: payload.handleApp,
    storeReq: payload.handleReq,
    storeUpdate: payload.handleUpdate,
    storeDrivers: payload.handleDrivers
  }
}

export default connect(mapStateToProps, {bindApp, bindReq, bindUpdate, bindDrivers})(App);


