import React from 'react';
import Button from '@material-ui/core/Button';
import {withStyles, withTheme} from "@material-ui/core/styles";
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import MenuList from '@material-ui/core/MenuList';
import icon from "../select.svg";
import 'amcharts3';
import 'amcharts3/amcharts/serial';
import 'amcharts3/amcharts/themes/light';
import 'amcharts3/amcharts/plugins/export/export.min.js';
import 'amcharts3/amcharts/plugins/export/export.css';
import AmCharts from '@amcharts/amcharts3-react';
import Legend from "./legend";
import filter from "../filter.svg";

class Plot extends React.Component {

    amchartsSettings =
        {
            "type": "serial",
            "path": "/",
            "svgIcons": "false",
            "categoryField": "category",
            "categoryAxis": {
                "gridPosition": "start",
                "axisAlpha": 0,
                "color": "#748AA1",
                "gridThickness": 0
            },
            "chartScrollbar": {
                "enabled": true,
                "backgroundColor": "#E5E9F2",
                "dragIcon": "dragIconRoundSmall",
                "dragIconWidth": 36,
                "offset": -2,
                "oppositeAxis": false,
                "scrollbarHeight": 4,
                "selectedBackgroundColor": "#B8C5D3"
            },
            "trendLines": [],
            "graphs": [
                {
                    "balloonText": "[[title]] of [[category]]:[[value]]",
                    "bulletBorderThickness": 0,
                    "id": "AmGraph-1",
                    "lineColor": "#4072EE",
                    "lineThickness": 2,
                    "title": "graph 1",
                    "type": "smoothedLine",
                    "valueField": "column-1"
                },
                {
                    "balloonText": "[[title]] of [[category]]:[[value]]",
                    "id": "AmGraph-2",
                    "lineColor": "#FEC400",
                    "lineThickness": 2,
                    "title": "graph 2",
                    "type": "smoothedLine",
                    "valueField": "column-2"
                }
            ],
            "guides": [],
            "valueAxes": [
                {
                    "id": "ValueAxis-1",
                    "zeroGridAlpha": 0,
                    "axisAlpha": 0,
                    "color": "#748AA1",
                    "gridAlpha": 1,
                    "gridColor": "#EBEDF4",
                    "minorGridAlpha": 0,
                    "title": "Руб",
                    "titleColor": "#748AA1",
                    "titleRotation": 1,
                    "titleBold": false
                }
            ],
            "allLabels": [],
            "balloon": {
                "offsetX": 0,//
                "offsetY": 0
            },
            "titles": [],
            "dataProvider": [
                {
                    "category": "category 1",
                    "column-1": 8,
                    "column-2": 5
                },
                {
                    "category": "category 2",
                    "column-1": 6,
                    "column-2": 7
                },
                {
                    "category": "category 3",
                    "column-1": 2,
                    "column-2": 3
                },
                {
                    "category": "category 4",
                    "column-1": 1,
                    "column-2": 3
                },
                {
                    "category": "category 5",
                    "column-1": 2,
                    "column-2": 1
                },
                {
                    "category": "category 6",
                    "column-1": 3,
                    "column-2": 2
                },
                {
                    "category": "category 7",
                    "column-1": 6,
                    "column-2": 8
                }
            ]
        };

    render() {

        const { classes } = this.props;

        return (
            <div className={classes.plotWrapper}>
                <div className={classes.infoWrapper}>
                    <div className={classes.titleWrapper}>
                        <span>Бюджет в разрезе распорядителей</span>
                    </div>
                    <div className={classes.absolutReposition}>
                        <Legend data={[{color:"#4274EE", title:"Фирма 1"},{color:"#FEC400", title:"Фирма 2"}]}/>
                        <div className={classes.filterWrapper}>
                            <span className={classes.filterTitle}>Фильтр</span>
                            <div className={classes.iconWrapper}>
                                <img alt={"T"} className={classes.icon} src={filter}/>
                            </div>
                        </div>
                    </div>
                </div>
                <AmCharts.React
                    key={0}
                    className={classes.chartWrapper}
                    options={this.amchartsSettings}
                />
            </div>
        );
    }
}

const styles = theme => ({
    absolutReposition:{
        position: "absolute",
        right: 0,
        display: "flex",
        paddingRight: 33.3
    },
    filterWrapper:{
        display: "flex"
    },
    iconWrapper:{
        marginLeft: 20,
        backgroundColor: "#FFFFFF",
        boxShadow: "0px 1px 4px #E5E9F2",
        borderRadius: 5
    },
    icon:{},
    filterTitle:{},
    infoWrapper: {
        display: "flex",
        position: "relative",
        paddingTop: 24,
        height: 25
    },
    titleWrapper: {
        fontFamily: "Roboto",
        fontWeight: "500",
        fontSize: 20,
        marginLeft: 33.3
    },
    chartWrapper: {
        position: "absolute",
        top: 50,
        bottom: 0,
        left: 0,
        right: 0
    },
    list:{
        paddingTop:13,
        paddingBottom:13
    },
    selected:{

    },
    root:{
        fontSize: 14,
        color: "#748AA1",
        height: 14,
        paddingTop:7,
        paddingBottom:7,
        paddingLeft:20,
        paddingRight:20,
        "&:hover":{
            backgroundColor: "unset",
            color: "#4072EE"
        }
    },
    current:{color:"red"},
    imageWrapper:{
        width:17,
        height:17,
        marginLeft: 5
    },
    plotWrapper: {
        position: "absolute",
        top:0,
        bottom:0,
        left:0,
        right:0,
        background: "#FFFFFF",
        boxShadow: "0px 1px 4px #E5E9F2",
        borderRadius: 5,
        marginTop: 10,
        marginBottom: 5
    },

});

export default withStyles(styles)(withTheme()(Plot));


