import { handleActions } from 'redux-actions';
import * as actions from './actions';

//дефолтные значения storage
const app = {
  title: "OF",
  pagesName: ["KPI - Группа", "OPEX - Группа","CIB","КБ","РБ","Чувствит-ть"],
  template: "white",
  menu: true,
  menuPos: "right"
},
date     = { date: 2},
router   = { page: 0 },
update   = {
  state: 0,
  preloader: false,
  scroll: false
},
category = {category: "Общие"},
drivers  = {
  driversData : null
},
req = {
  drivers: null,
  data: null
},
matrix = {
  data: null,
  btns: null
},
buttons = {
  CIR: -1,
  COR: -1,
  NIM: -1,
  CAGR: -1
};

//Общий store приложения
export const handleApp = handleActions({
  [actions.bindApp](state, { payload }) {
    return { ...state, ...payload };
  },
}, app);

//Store для запросов
export const handleReq = handleActions({
  [actions.bindReq](state, { payload }) {
    return { ...state, ...payload };
  },
}, req);

//Store для Роутера страниц
export const handleRouter = handleActions({
  [actions.bindRouter](state, { payload }) {
    return { ...state, ...payload };
  },
}, router);

//Store для Изменения даты
export const handleDate = handleActions({
  [actions.bindDate](state, { payload }) {
    return { ...state, ...payload };
  },
}, date);

//Store для категорий драйверов
export const handleCategory = handleActions({
  [actions.bindCategory](state, { payload }) {
    return { ...state, ...payload };
  },
}, category);


//Store для драйверов
export const handleDrivers = handleActions({
  [actions.bindDrivers](state, { payload }) {
    return { ...state, ...payload };
  },
}, drivers);

//Store для Обновления плиток
export const handleUpdate = handleActions({
  [actions.bindUpdate](state, { payload }) {
    return { ...state, ...payload };
  },
}, update);


//Store для матрицы
export const handleMatrix = handleActions({
  [actions.bindMatrix](state, { payload }) {
    return { ...state, ...payload };
  },
}, matrix);

//Store для кнопок матрицы
export const handleButtons = handleActions({
    [actions.bindButtons](state, { payload }) {
      return { ...state, ...payload };
    },
}, buttons);


