import { createAction } from 'redux-actions';

//action общий для приложения
export const bindApp = createAction('ACTION_START');
//action для запросов
export const bindReq = createAction('ACTION_REQ');
//action для переключателя страниц
export const bindRouter = createAction('ACTION_ROUTER');
//action для переключения годов
export const bindDate = createAction('ACTION_DATE');
//action для переключения категорий драйверов
export const bindCategory = createAction('ACTION_СATEGORY');
//action для драйверов
export const bindDrivers = createAction('ACTION_DRIVES');
//action для обновления страниц
export const bindUpdate = createAction('ACTION_UPDATE');
//action для Матрицы
export const bindMatrix = createAction('ACTION_MATRIX');
//action для кнопок матрицы
export const bindButtons = createAction('ACTION_BUTTONS');


