import React from 'react';
import Button from '@material-ui/core/Button';
import {withStyles, withTheme} from "@material-ui/core/styles";
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import MenuList from '@material-ui/core/MenuList';
import icon from "../select.svg";

class Legend extends React.Component {

    render() {

        const { classes } = this.props;

        const legendItems = this.props.data.map((value, key) => {
            return(
                <div className={classes.legendItem} key={key}>
                    <div className={classes.colorDiv} style={{backgroundColor: value.color}}/>
                    <div className={classes.titleWrapper}>
                        <span className={classes.title}>{value.title}</span>
                    </div>
                </div>
            )
        });

        return (
            <div className={classes.legendWrapper}>
                {legendItems}
            </div>
        );
    }
}

const styles = theme => ({
    legendItem:{
        display:"flex",
        marginLeft: 25
    },
    colorDiv:{
        width: 35.33,
        height: 6,
        borderRadius: 3,
        marginRight: 5,
        marginTop: 7
    },
    titleWrapper:{

    },
    title:{},
    legendWrapper:{
        display: "flex",
        marginRight: 30
    }
});

export default withStyles(styles)(withTheme()(Legend));



