import React from 'react';
import Button from '@material-ui/core/Button';
import {withStyles, withTheme} from "@material-ui/core/styles";
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import MenuList from '@material-ui/core/MenuList';
import icon from "../select.svg";

class Select extends React.Component {

    state = {
        anchorEl: null,
        click: null
    };

    handleClick = event => {
        this.setState({ anchorEl: event.currentTarget, click: (this.state.click === null) ? true : !this.state.click });
    };

    handleClose = () => {
        this.setState({ anchorEl: null, click: !this.state.click});
    };

    render() {
        const { anchorEl,click } = this.state;

        const { classes } = this.props;

        let cls = "";

        switch(click){
            case true:
                cls = classes.active;
                break;
            case false:
                cls = classes.unactive;
        }

        return (
            <div className={classes.selectWrapper}>
                <div className={classes.valueWrapper} onClick={this.handleClick}>
                    <div className={classes.currentValue}>
                        {this.props.currVal}
                    </div>
                    <div className={classes.imageWrapper}>
                        <img alt={"V"} src={icon} className={cls}/>
                    </div>
                </div>
                <Menu
                    id="simple-menu"
                    anchorEl={anchorEl}
                    open={Boolean(anchorEl)}
                    onClose={this.handleClose}
                    classes={{paper:classes.paper}}
                    MenuListProps={{classes:{root:classes.list}}}//
                >
                    {this.props.items.map((value,key) => {return <MenuItem onClick={this.handleClose} key={key} classes={{root:classes.root, selected:classes.selected}}>{value}</MenuItem>})}
                </Menu>
            </div>
        );
    }
}

const styles = theme => ({
    list:{
        paddingTop:13,
        paddingBottom:13
    },
    selected:{

    },
    root:{
        fontSize: 14,
        color: "#748AA1",
        height: 14,
        paddingTop:7,
        paddingBottom:7,
        paddingLeft:20,
        paddingRight:20,
        "&:hover":{
            backgroundColor: "unset",
            color: "#4072EE"
        }
    },
    current:{color:"red"},
    imageWrapper:{
        width:17,
        height:17,
        marginLeft: 5,
        marginTop: 2.5
    },
    paper:{
        marginTop:40,
        background: "#FFFFFF",
        border: "1px solid #EBEDF4",
        boxSizing: "border-box",
        boxShadow: "0px 5px 15px rgba(116, 138, 161, 0.25)",
        borderRadius: 5,
    },
    active:{
        margin: "auto",
        transition: "all 300ms ease-in-out",
        transform: "rotateX(180deg)",
        transformOrigin: "50% 33%"
    },
    unactive:{
        margin: "auto",
        transition: "all 300ms ease-in-out",
        transform: "rotateX(0deg)",
        transformOrigin: "50% 33%"
    },
    valueWrapper: {
        display: "flex"
    },
    selectWrapper: {
        marginTop: 27.75,
        marginLeft: 10
    },
    currentValue: {
        height: 22.5,
        fontSize: 14,
        color: "#748AA1",
        lineHeight: "20px",
        whiteSpace: "nowrap"
    }
});

export default withStyles(styles)(withTheme()(Select));



