import React, {Component} from "react";
import {withStyles, withTheme} from "@material-ui/core/styles";
import user from "../User.png";
import FormItem from "./formItem";

class UserMenu extends Component {

    render() {
        const { classes } = this.props;

        const data = [
            {
                id: 0,
                text: "Отчёт по исполнению бюджет"
            },
            {
                id: 1,
                text: "Прямые расходы ГОСБ"
            },
            {
                id: 2,
                text: "Отчёт по исполнению бюджета"
            },
            {
                id: 3,
                text: "Отчёт по исполнению бюджета"
            }
            ];
        let allForms = data.map((value,key)=>{
           return  <FormItem key={key} id={value.id} text={value.text}/>
        });

        return (
            <div className={classes.leftMenuWrapper}>
                <div className={classes.userMenuWrapper}>
                    <div className={classes.userWrapper}>
                        <div className={classes.avatarWrapper}>
                            <div className={classes.crop} ><img className={classes.avatar} src={user} alt="аватар"/></div>
                            <div className={classes.status}/>
                        </div>
                        <div className={classes.userName}><span>Орехов<br/>Борис Вениаминович</span></div>
                    </div>
                    <div className={classes.buttonsWrapper}>
                        <span className={classes.exit}>
                            Выйти
                        </span>
                        <span className={classes.edit}>
                            Редактивровать
                        </span>
                    </div>
                </div>
                <div className={classes.hr}/>
                <div className={classes.fromWrapper}>
                    <span className={classes.formListTitle}>Отчётные формы</span>
                    {allForms}

                </div>
            </div>
        )
    }
}

const styles = theme => ({
    userMenuWrapper:{
        "&:hover":{
            "& $buttonsWrapper":{
                opacity: 1
            },
            "& $crop":{
                boxShadow: "0px 4px 4px rgba(0, 0, 0, 0.25)"
            }
        },
        height:90
    },
    exit:{
        fontStyle: "normal",
        fontWeight: "300",
        lineHeight: "normal",
        fontSize: 12,
        color: "#748AA1",
        marginRight: 10,
        "&:hover":{
            color: "#4072EE"
        }
},
    buttonsWrapper:{
        display: "flex",
        marginLeft: 90,
        opacity: 0
    },
    edit:{
        fontStyle: "normal",
        fontWeight: "300",
        lineHeight: "normal",
        fontSize: 12,
        color: "#748AA1",
        "&:hover":{
            color: "#4072EE"
        }
    },
    leftMenuWrapper: {
        //position: "absolute",
        minWidth: 360,
        backgroundColor: theme.palette.primary.userMenuBackground,
        boxShadow: "0px 0px 5px #E5E9F2",
        //left: "0%",
        //right: "75.69%",
        //top: "0%",
        //bottom: "0%",
        marginRight:5,
        minHeight: 735
    },
    userName: {
        padding: 8
    },
    avatar: {
        marginTop: 0,
        marginLeft: -23,
        height: 71
    },
    avatarWrapper: {
        position: "relative",
        marginRight: 15
    },
    crop: {
        width: 54,
        height: 54,
        overflow: "hidden",
        borderRadius: "50%"
    },
    status: {
        width: 13.5,
        height: 13.5,
        position: "absolute",
        top: 0,
        right: 0,
        background: "#29CB97",
        border: "2.7px solid #FFFFFF",
        boxSizing: "border-box",
        boxShadow: "0px 1.35px 6.75px rgba(0, 0, 0, 0.05)",
        borderRadius: 6.75
    },
    userWrapper: {
        display: "flex",
        width: "95%",
        marginTop: 19,
        marginLeft: 13
    },
    tileInner: {
        width: "100%",//
        position: "relative",
        height: "100%",
        boxSizing: "border-box",
        background: theme.palette.primary.tiles,
        padding: "5px"
    },
    hr: {
        backgroundColor: "#EBEDF4",
        height: 1,
        width: "90%",
        marginLeft: "5%",
        marginBottom: 20
    },
    fromWrapper: {


    },
    formListTitle: {
        fontFamily: "Roboto",
        fontStyle: "normal",
        fontWeight: 500,
        lineHeight: "normal",
        fontSize: "20px",
        color: "#000000",
        marginLeft: 22
    }
});

export default withStyles(styles)(withTheme()(UserMenu));


